<script>
import { onMount,onDestroy } from 'svelte';
import Slide from './slide.svelte';
export let Data;
let selected = 0;
let buffer = Data;
let interval=null;
let transed = false;

buffer[selected].active = true;

function add(){
   buffer[selected].active = false;
   selected = (selected + 1)%buffer.length;
   buffer[selected].active = true;
   transed = true;
}

function sub(){
    buffer[selected].active = false;
    selected = selected - 1;
    if(selected<0)
        selected = buffer.length - 1;
    buffer[selected].active = true;
    transed = true;
}

function automatic(){
    if(!transed)
        add();
    transed = false;
}

onMount(function(){
    interval = setInterval(automatic,10000);
})
onDestroy(function(){
    clearInterval(interval);
})
</script>
<style lang="text/postcss">.slide-container{margin:auto;width:100vw;height:16rem;overflow:hidden;position:relative}.button-group{position:absolute;top:0;right:0;bottom:0;left:0;display:flex;align-items:center;justify-content:space-between}.left,.right{border-radius:9999px;--border-opacity:1;border-color:#fff;border-color:rgba(255,255,255,var(--border-opacity));border-width:2px;padding:1rem;--text-opacity:1;color:#fff;color:rgba(255,255,255,var(--text-opacity));font-size:1.25rem;font-weight:900;z-index:10}.left:focus,.right:focus{outline:0}.left:hover,.right:hover{--border-opacity:1;border-color:#90cdf4;border-color:rgba(144,205,244,var(--border-opacity));--text-opacity:1;color:#000;color:rgba(0,0,0,var(--text-opacity));--bg-opacity:1;background-color:#90cdf4;background-color:rgba(144,205,244,var(--bg-opacity))}
/*# sourceMappingURL=src/components/utils/slider/slider.svelte.map */</style>

<div class="slide-container">
    <div class='button-group'>
        <button class="left" on:click={sub}>&lt;</button>
        <button class="right" on:click={add}>&gt;</button>
    </div>
    {#each buffer as slide}
        <Slide {slide}/>
    {/each}
</div>